*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input,
select {
  vertical-align: middle;
}

/* BREAKPOINTS */
/* COLORS */
/* ROOT VARIABLES */
:root {
  /* FONTS */
  --fs-400: 1rem;
  --fs-600: 1.4rem;
  --fs-800: 1.7rem;
  --lh-400: 1.875rem;
  --lh-600: 2.25rem;
  --lh-800: 3.2rem;
  /* SPACES */
  --base-space: 28px;
}

/* RESPONSIVE VARIABLE UPDATES */
@media (min-width: 40em) {
  :root {
    --fs-400: 1.125rem;
    --fs-600: 1.5rem;
    --fs-800: 2.5rem;
    --lh-400: 1.92rem;
    --lh-600: 2.35rem;
    --lh-800: 3.8rem;
    --base-space: 30px;
  }
}
@media (min-width: 122em) {
  :root {
    --fs-400: 1.3rem;
    --fs-600: 1.7rem;
    --fs-800: 3rem;
    --lh-400: 2.3rem;
    --lh-600: 2.6rem;
    --lh-800: 3.8rem;
    --base-space: 40px;
  }
}
@font-face {
  font-family: "League Spartan Bold";
  src: local("League Spartan Bold"), local("League-Spartan-Bold"), url("../fonts/leaguespartan-bold-webfont.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Space Mono Bold";
  src: url("../fonts/spacemono-bold-webfont.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Space Mono Bold Italic";
  src: url("../fonts/spacemono-bolditalic-webfont.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Space Mono Italic";
  src: url("../fonts/spacemono-italic-webfont.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Space Mono Regular";
  src: url("../fonts/spacemono-regular-webfont.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
button {
  padding: 0;
  margin: 0;
}

button, a.btn-as-link {
  border-style: none;
  padding: 7px 14px;
  background-color: #3CAEA3;
  color: #191919;
  border: solid 4px transparent;
  font-size: var(--fs-400);
  cursor: pointer;
  transition: 0.4s;
  position: relative;
}
button:hover, a.btn-as-link:hover {
  border: solid 4px #3CAEA3;
  color: #3CAEA3;
  background-color: initial;
}
button:focus, a.btn-as-link:focus {
  box-shadow: 0 0 0 0.27rem rgba(60, 174, 163, 0.5);
}

/* BASE */
::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  background-color: #191919;
}

::-webkit-scrollbar-thumb {
  background: #3CAEA3;
  border-radius: 1px;
  border-left: solid 7px #3CAEA3;
}

::selection {
  background-color: #3CAEA3;
}

body {
  font-family: "Space Mono Regular";
  letter-spacing: 0.01rem;
  color: #E4E6E8;
  padding-bottom: 180px;
  background-color: #191919;
}

img {
  max-width: 100%;
}

a {
  color: #3CAEA3;
  text-decoration: none;
}

p, span {
  font-size: var(--fs-400);
  line-height: var(--lh-400);
}

/* HEADER */
.about-me {
  margin: 0 auto;
  max-width: 1000px;
  padding: var(--base-space);
}
.about-me .greeting {
  margin: var(--base-space) 0;
  padding: 0 0 var(--base-space) 0;
  font-size: var(--fs-800);
  line-height: var(--lh-800);
}
@media (min-width: 768px) {
  .about-me .greeting {
    padding: var(--base-space) 0;
  }
}
.about-me .greeting mark {
  background: #3CAEA3;
  color: #191919;
  padding: 8px 14px;
}
.about-me .btn-resume {
  margin-top: 100px;
}
.about-me .details {
  margin: var(--base-space) 0;
}

/* MAIN */
.recent-works {
  max-width: 1000px;
  margin: 0 auto;
  padding: var(--base-space);
}

.recent-works h3 {
  font-size: var(--fs-400);
}
.recent-works p {
  margin: 15px 0;
}
.recent-works p:last-child {
  margin-bottom: 0;
}

/* NAV */
.nav-social {
  position: fixed;
  inset: auto 0 0 0;
  padding: 10px;
  background-color: #191919;
  animation: fadeInUp 0.35s both;
}
@media (min-width: 576px) {
  .nav-social {
    padding: 18px;
  }
}
@media (orientation: landscape) {
  .nav-social {
    inset: 0 0 0 auto;
    padding: 30px;
  }
  .nav-social ul {
    flex-direction: column;
  }
}
.nav-social ul {
  display: flex;
  height: 100%;
  max-width: 550px;
  margin: 0 auto;
}
.nav-social ul li {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav-social ul li a {
  color: #E4E6E8;
  font-size: 20px;
}
@media (min-width: 576px) {
  .nav-social ul li a {
    font-size: 27px;
  }
}